.searchField {
  position: relative;
  padding: 10px 0 0;
  /* margin-top: 10px; */
  width: 50%; }

.searchInput {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid black;
  /*  $brown-place; */
  outline: 0;
  font-size: 1.3rem;
  color: white;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s; }
  .searchInput::-webkit-input-placeholder {
    color: #ccc8b8; }
  .searchInput::-ms-input-placeholder {
    color: #ccc8b8; }
  .searchInput::placeholder {
    color: #ccc8b8; }
  .searchInput:placeholder-shown ~ .searchLabel {
    font-size: 1.3rem;
    cursor: text;
    top: 20px; }

.searchLabel {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: black;
  /* $brown-place; */ }

.searchInput:focus {
  padding-bottom: 6px;
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, black, white);
  border-image-slice: 1; }
  .searchInput:focus ~ .searchLabel {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: black;
    font-weight: 700; }

.searchInput:required, .searchInput:invalid {
  box-shadow: none; }

